// app
.App {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-color: #f1f4f6;
  &__header {
    position: relative;
    .header {
      position: fixed;
      display: flex;
      align-items: center;
      align-content: center;
      z-index: 10;
      width: 100%;
      height: 60px;
      background-color: #fafbfc;
      box-shadow: 0 1px 0.5px rgba(#000000, .05);
      &__logo {
        display: none; }
      &__content {
        position: relative;
        display: flex;
        align-content: center;
        align-items: center;
        padding-left: 280px;
        height: 60px;
        flex: 1;
        &__left,
        &__right {
          display: flex;
          align-items: center;
          padding-left: 15px;
          flex: 1; }
        &__right {
          text-align: left;
          border: 1px solid red; } }
      &__search {
        position: relative;
        height: 30px;
        width: 320px; }
      &__searchInput {
        width: 100%;
        height: 30px;
        float: left;
        padding: 0 40px 0 10px;
        font-size: 18px;
        line-height: 1.5;
        color: #000000;
        outline: none;
        &::focus {
          outline: none; } }
      &__searchCloseButton {
        @include button;
        position: absolute;
        top: 0;
        right: 0;
        height: 30px;
        padding: 0 10px;
        border: 1px solid #ddd; } } }

  &__sidebar {
    position: relative;
    .sidebar {
      position: fixed;
      display: flex;
      flex-direction: column;
      width: 280px;
      height: 100vh;
      z-index: 20;
      box-shadow: 7px 0 50px rgba($black, 0.05);
      overflow: hidden;
      transition: all .3s ease;
      &__logo {
        background-color: #fafbfc;
        height: 60px;
        width: 280px;
        padding: 0 15px;
        display: flex;
        align-items: center;
        &--src {
          margin-right: 120px;
          width: 100px;
          height: 23px; } }
      &__hamburger {
        @include button; }
      .ml-auto {
        margin-left: 0 !important;
        left: 0; }
      &__content {
        background-color: #ffffff;
        display: flex;
        width: 100%;
        flex-grow: 1;
        flex-flow: column;
        padding: 0 15px 15px;
        &Title {
          text-transform: uppercase;
          font-size: 18px;
          margin: 7px 0;
          font-weight: bold;
          color: darken($black, 10%);
          position: relative;
          white-space: nowrap;
          padding-right: 10px;
          width: 250px;
          text-overflow: ellipsis;
          overflow: hidden; }
        &List {
          position: relative;
          display: flex;
          flex-flow: column;
          text-decoration: none;
          cursor: pointer; }
        &Item {
          margin: 10px 0;
          padding: 10px 20px;
          &:first-child {
            margin-top: 0; }
          &:hover,
          &:active,
          &:focus {
            background-color: rgba($red, 0.5);
            border-top-right-radius: 50px;
            border-bottom-right-radius: 50px; } }
        &Link {
          text-decoration: none;
          cursor: pointer; }
        &Lable {
          font-size: 16px;
          line-height: 1.5;
          color: rgba($black, 0.5);
          font-weight: 500; } } } }
  &__main {
    position: relative;
    display: flex;
    flex-direction: column;
    z-index: 8;
    padding: 60px 0 0 280px;
    transition: all .2s;
    .main {
      padding: 30px 30px 0;
      .header {
        position: relative;
        padding: 30px;
        margin: -30px -30px 30px;
        background-color: rgba(#ffffff, 0.45);
        &Wrapper {
          position: relative;
          display: flex;
          align-items: center;
          align-contents: center; }
        &__Title {
          position: relative;
          display: inline-flex; }
        &__Icons {
          font-size: 1.5rem;
          display: flex;
          align-items: center;
          align-content: center;
          text-align: center;
          padding: 0.83333333rem;
          margin: 0 30px 0 0;
          background: #fff;
          box-shadow: 0 0.46875rem 2.1875rem rgba(4, 9, 20, 0.03), 0 0.9375rem 1.40625rem rgba(4, 9, 20, 0.03), 0 0.25rem 0.53125rem rgba(4, 9, 20, 0.05), 0 0.125rem 0.1875rem rgba(4, 9, 20, 0.03);
          border-radius: 0.25rem;
          width: 60px;
          height: 60px;
          border: 1px solid #dddddd;
          &Plane {
            margin: auto;
            position: relative;
            display: flex;
            align-content: center;
            align-items: center;
            justify-content: center;
            width: 36px;
            height: 36px; } }
        &__content {
          &Heading {
            font-size: 18px;
            line-height: 1.5;
            font-weight: 500; }
          &Subheading {
            font-size: 14px;
            line-height: 1.25;
            color: rgba($black, 0.3);
            opacity: .6;
            padding-top: 10px; } }
        &__Actions {
          margin-left: auto;
          .actions {
            &__dark,
            &__success {
              @include button;
              height: 33px;
              font-size: 14px;
              border: 1px solid transparent;
              border-radius: 4px; }
            &__dark {
              color: #fff;
              background-color: #343a40;
              border-color: #343a40;
              box-shadow: 0 0.125rem 0.625rem rgba(52, 58, 64, 0.4), 0 0.0625rem 0.125rem rgba(52, 58, 64, 0.5);
              margin-right: 1rem;
              width: 34px; }
            &__success {
              color: #fff;
              background-color: #3ac47d;
              border-color: #3ac47d;
              box-shadow: 0 0.125rem 0.625rem rgba(58, 196, 125, 0.4), 0 0.0625rem 0.125rem rgba(58, 196, 125, 0.5);
              padding: 0 10px; } } } } } }
  &__footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding-left: 280px;
    height: 60px;
    border-top: 1px solid rgba(#000000, 0.2);
    z-index: 12;
    overflow: hidden;
    .footer {
      height: 60px;
      padding: 15px 30px 0;
      background-color: #fafbfc; } } }



