@mixin clearfix {
  &:before,
  &:after {
    content: "";
    display: table; }
  &:after {
    clear: both; } }

@mixin placeholder {
  $placeholders: ':-moz' ':-webkit-input' '-moz' '-ms-input';
  @each $placeholder in $placeholders {
    &:#{$placeholder}-placeholder {
      @content; } } }

@mixin desktop {
  @media screen and (min-width: $desktop) {
    @content; } }

@mixin button {
  position: relative;
  // display: inline-block
  vertical-align: top;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  white-space: nowrap;
  text-rendering: auto;
  user-select: none;
  box-sizing: border-box;
  letter-spacing: 0;
  font-style: normal;
  box-shadow: none;
  font-family: $family-base;
  height: 56px;
  font-size: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  &:hover,
  &:focus {
    outline: none; } }


@mixin btnDefault {
    border-style: solid;
    border-width: 1px;
    color: $btn-default-color;
    fill: $btn-default-color;
    background-color: $btn-default;
    border-color: darken($btn-default, 5%) darken($btn-default, 10%) darken($btn-default, 15%);
    background: linear-gradient(to bottom, lighten($btn-default, 25%), $btn-default);
    box-shadow: 0 1px 0 rgba($white, 0.4) inset;
    &:hover {
        color: $btn-default-color;
        fill: $btn-default-color;
        background-color: darken($btn-default, 5%);
        background: linear-gradient(to bottom, $btn-default, $btn-default); } }

