@mixin input {
  align-items: center;
  border: 1px solid transparent;
  // border-radius: 6px
  box-shadow: none;
  display: inline-flex;
  // height: 56px
  justify-content: flex-start;
  line-height: 1.5;
  // padding: 6px 15px
  position: relative;
  vertical-align: top;
  // border-color: $gray
  color: $black;
  box-shadow: none;
  max-width: 100%;
  width: 100%;
  transition: all 0.1s ease-out;

  &:focus,
  &:active {
    // border-color: $black
    box-shadow: none;
    outline: none;
 }    // background-color: #fff

  @include placeholder {
    color: #bbbbbb; } }
