// 1. Initial variables

// Colors

$black: #000000 !default;
$gray: #808080 !default;
$light: #9e9e9e !default;
$lighter: #f0f0f0 !default;
$white: #ffffff !default;

$svg: #c2c5cc;
$purple: #783392 !default;
$yellow: #fcbf14 !default;
$red: #ee395f !default;
$green: #41b468 !default;
$cyan: #74cdda !default;

// Typography

$family-sans-serif: "Rubik", "Noto Sans Bengali", "Helvetica Neue", "Helvetica", "Arial", sans-serif !default;
$family-base: $family-sans-serif !default;

// Scaffolding

$body-background: $white !default;
$body-rendering: optimizeLegibility !default;
$body-family: $family-sans-serif !default;
$body-text: $black !default;
$body-line-height: 2;
$body-weight: 400 !default;
$body-size: 16px !default;

$heading-line-height: 1.22;

// Breakpoints

$desktop: 1000px !default;

// Miscellaneous

$easing: ease-out !default;
$speed: 86ms !default;


// Button

$btn-default: $light;
$btn-default-color: $black;

$btn-yellow: $yellow;
$btn-yellow-color: $purple;

$btn-purple: $purple;
$btn-purple-color: $yellow;

$btn-green: $green;
$btn-green-color: $white;

$btn-red: $red;
$btn-red-color: $white;
