
.field {
  margin-top: 30px;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  &:not(:last-child) {
    margin-bottom: 20px; }

  $root: &;

  &__label {
    display: block;
    color: #333;
    fill: #333;
    font-size: 2em;
    line-height: 1.33;
    font-weight: 400;
    letter-spacing: -0.03em;
    margin-bottom: 6px; }

  &__input {
    align-items: center;
    border: 1px solid transparent;
    border-radius: 6px;
    box-shadow: none;
    display: inline-flex;
    height: 56px;
    justify-content: flex-start;
    line-height: 1.5;
    padding: 6px 15px;
    position: relative;
    vertical-align: top;
    border-color: $gray;
    color: $black;
    box-shadow: none;
    max-width: 100%;
    width: 100%;
    font-size: 2.6em;
    font-weight: 700;
    transition: all 0.1s ease-out;

    &:focus,
    &:active {
      border-color: $black;
      box-shadow: none;
      outline: none;
      background-color: #fff; }

    @include placeholder {
      color: #bbbbbb; } }

  &__error {
    display: block;
    font-size: 1.6em;
    margin-top: 6px;
    margin-bottom: 0;
    padding-left: 15px;
    padding-right: 15px;
    color: darken($red, 10%);
    background-color: lighten($red, 35%);
    border: 1px solid lighten($red, 20%);
    border-radius: 6px; }

  &.joma {
    #{$root}__label,
    #{$root}__input {
      color: darken($green, 10%); } }
  &.baki {
    #{$root}__label,
    #{$root}__input {
      color: $red; } } }



.submitButton {
  @include button;
  @include btnGreen;
  width: 100%;
  border-radius: 6px;
  &.baki {
    @include btnRed; }
  &.joma {
    @include btnGreen; }
  &.disabled {
    pointer-events: none;
    opacity: .5; } }

